@import "variables";
@import "libs.min";
@import "default";



.container{
    width: 1140px;
    padding: 10px;
}

.line{
    display: inline-block;
    background: #000;
    width: 50px;
    height: 2px;
    align-items: center;
    margin-bottom: 30px;
}

header{
    display: flex;
    justify-content: center;
    height: 25px;
    width: 100%;
    position: absolute;

    .wrapp__menu{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin-top: 40px;

        .logo{
            width: 20%;
            color: #fff;
        }
        
        nav{
            width: 55%;

            ul{
                display: flex;
                justify-content: space-between;
                color: #fff;

            }
        }
    }
}

.mp__s0{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100vh;
    width: 100%;

    .img__wrapp{
        background: #000;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -2;
        height: 100vh;

        img{
            opacity: 0.55;
            height: 100vh;
        }
    }

    .header__centr{
        align-items: center;
        text-align: center;
        width: 100%;
        color: #fff;

        h2{
            margin-bottom: 30px;
            font-size: 48px;
        }

        p{
            margin-bottom: 55px;
            font-size: 20px;
        }
        button{
            background: 0;
            border: 2px solid #fff;
            width: 190px;
            height: 50px;
            color: #fff;
        }
    }
}


.mp__s1{
    display: flex;
    justify-content: center;
    text-align: center;
    height: 580px;

    .h__services{
        height: 160px;

        h3{
            margin-top: 100px;
            margin-bottom: 30px;
        }

        .line{
            display: inline-block;
            background: #000;
            width: 50px;
            height: 2px;
            align-items: center;
            margin-bottom: 30px;
        }
    }

    .advantage__block{
        display: flex;
        justify-content: space-between;
        width: 100%;

        .block__conent{
            display: flex;
            align-items: center;
            flex-direction: column;
            text-align: center;
            width: 33%;
            height: 320px;
            margin: 0 10px;

            .img__wrapp__2{
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                border: 2px solid grey;
                width: 85px;
                height: 85px;
                border-radius: 50%;
                margin-bottom: 30px;
                font-size: 48px;

                img{
                    width: 40px;
                }
            }

            h3{
                margin-bottom: 25px;
            }

        }

        .bg__color{
            background: #c0301c;
        }

        .size__color{
            color:#c0301c ;
        }
    }
}

.mp__s2{
    display: flex;
    justify-content: center;
    background: #f8f8f8;
    height: 850px;

    .about__header{
        text-align: center;
        margin-top: 100px;
        margin-bottom: 70px;
        
        h3{
            margin-bottom: 35px;
        }
    }

    .about__body{
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 75px;

        .about__content{
            width: 45%;
            margin: 0 15px;
        }
    }

    .slider{
        display: flex;
        justify-content: space-between;
        text-align: center;
        flex-wrap: wrap;
        width: 100%;
        height: 310px;

        .joint__wrapp{
            width: 23%;
            
            .img__wrapp{
                display: flex;
                justify-content: center;
                align-items: flex-end;
                background: black;
                position: relative;
                height: 250px;
                margin-bottom: 20px;

                .bg__img{
                    position: absolute;
                    height: 100%;
                    transition: 0.35s;
                    z-index: 10;
                }

                &:hover{
                    > img{
                        opacity: 0.45;
                    }
                }

                .wrapp__soc{
                    display: flex;
                    justify-content: space-between;
                    height: 20px;
                    width: 115px;
                    margin-bottom: 40px;
                    opacity: 0;
                    transition: 0.75s;
                    z-index: 20;

                    .bg__wrapp__soc{
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        width: 95%;
                        height: 20px;
                        position: relative;
                        padding: 3px;
                        cursor: pointer;

                        &:hover{
                            background: #c0301c;
                        }
                        
                        img{
                            width: auto;
                            height: auto;
                        }
                    }
                }

                &:hover{
                    .wrapp__soc {
                        opacity: 1;
                    }
                }

            }

            h3{
                margin-bottom: 20px;
            }
        }
    }      
}

.mp__s3{
    display: flex;
    justify-content: center;
    height: 1380px;

    .latest__header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align: center;
        flex-direction: column;
        margin-top: 90px;

        h3{
            margin-bottom: 30px;
        }
        
        ul{
            display: flex;
            justify-content: space-between;
            width: 420px;
            padding: 0 10px;

            a{
                &:hover{
                    color: #c0301c;
                }
            }
        }
    }

    .galery{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        height: 1025px;
        margin-top: 70px;

        .img__wrapp__galery{
            display: flex;
            justify-content: center;
            align-items: center;
            background: #b55351;
            flex-direction: column;
            position: relative;
            height: 320px;
            width: 30%;
            color: #fff;
        
            img{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 320px;
                transition: 0.5s;
                object-fit: cover;
            }

                h3{
                    margin-bottom: 20px;
                }

                p{
                    margin-bottom: 25px;
                }
                button{
                    border: 0;
                    height: 35px;
                    width: 100px;
                    color: #b55351;
                }

                
                &:hover{
                    img{
                    opacity: 0.25;
                    }
                    h3, p, button{
                        z-index: 100;
                    }
                }
            }
    }
}

.mp__s4{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    height: 370px;
    width: 100%;
    color: #fff;
    margin-bottom: 90px;

    .img__wrapp{
        background: #b44621;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;

        img{
            height: 100%;
            transition: 0.5s;
            object-fit: cover;
        }
    }

    h3{
        margin-bottom: 50px;
    }

    button{
        background: 0;
        border: 2px solid #fff;
        height: 50px;
        width: 175px;
        color: #fff;
    }

    &:hover{
        .img__wrapp{
            z-index:-2;
        }
        img{
            opacity: 0.4;
            z-index: -1;
        }
    }
}

.mp__s5{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 1075px;
    width: 100%;

    .header__section{
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        flex-direction: column;
        margin-bottom: 70px;

        h3{
            margin-bottom: 30px;
        }
    }

    .body__wrapp{
        display: flex;
        flex-direction: column;

        .long__line{
            background: #dddddd;
            height: 2px;
            width: 100%;
            margin-bottom: 40px;
        }
        .sturtup{
            display: flex;
            justify-content: space-between;
            height: 320px;
            width: 100%;
            margin-bottom: 40px;

            img{
                width: 40%;
                object-fit: cover;
            }

            .content{
                display: flex;
                flex-direction: column;
                height: 285px;
                width: 55%;

                .date{
                    display: flex;
                    margin-bottom: 25px;
                
                    .block{
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        flex-direction: column;
                        border: 1px solid #000;
                        height: 80px;
                        width: 70px;
                        margin-right: 30px;
                    }


                        h3{
                            margin-bottom: 20px;
                        }
                }

                a{
                    &:hover{
                        color: #c43d2a;
                    }
                } 

                p{
                    margin-bottom: 35px;
                }
            }        
        }
    }
}

.mp__s6{
    display: flex;
    align-items: center;
    text-align: center;
    flex-direction: column;
    background: #f8f8f8;
    height: 475px;
    width: 100%;


    .wrapp__section{
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;

    
        .sponsor__list{
            display: flex;
            justify-content: space-between;
            align-items: center;
            text-align: center;
            height: 70px;
            width: 100%;
            margin-top: 100px;
            margin-bottom: 75px;
        }

        .word{
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            width: 100%;

            p{
                margin-bottom: 35px;
            }
        
        }
    }
}

.mp__s7{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    width: 100%;

    .header__section{
        margin-top: 100px;

        h3{
            margin-bottom: 25px;
        }
    }

    .body__section{
        display: flex;
        justify-content: space-between;
        margin-top: 80px;
        height: 100%;

        nav{
            display: flex;
            flex-direction: column;
            width: 25%;

            .nav{  
                display: flex;
                justify-content: space-between;
                margin-bottom: 40px;
                width: 100%;

                .img__wrapp{
                    height: 20px;
                    margin-right: 10px;
                }
            
                .contacts{
                    display: flex;
                    flex-direction: column;
                    width: 255px;
                    text-align: left;

                    h3{
                        margin-bottom: 15px;
                    }

                    p{
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }

        img{
            width: 15px;
            height: auto;
        }

        .input__wrapp{
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            height: 300px;
            width: 65%;
        }

            .input{
                display: flex;
                justify-content: space-between;
                height: 110px;
                width: 100%;

                input{
                    height: 45px;
                    width: 48%;
                }
            }

            textarea{
                height: 105px;
                width: 100%;
                resize: none;
            }

            .input__footer{
                display: flex;
                text-align: left;
                margin-top: 20px;

                button{
                    background: 0;
                    border: 1px solid #000;
                    height: 45px;
                    width: 170px;
                    margin-right: 40px;
                }
                
                span{
                    color:#c43d2a ;
                }
            }
}


footer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    background: #c0301c;
    height: 305px;
    color: #fff;

    .soc__wrapp{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 55px;
        width: 220px;
        margin-bottom: 30px;

        .img__wrapp{
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px solid #fff;
            height: 25px;
            width: 25px;
            border-radius: 50%;
            margin: 0 7.5px;

            a{
                display: flex;
                align-items: center;
                justify-content: center;

                img{
                    height: auto;
                    width: auto;
                }
            }
        }
    }

    .wiht__line{
        height: 2px;
        width: 50px;
        background: #fff;
        margin-bottom: 30px;
    }
}

//breakpoints 576px(mob) 768px(tabled) 992px 1200px

@media screen and (max-width: 1199px){
    .container{
        width: 960px;
    }

}

@media screen and (max-width: 991px){
    .container{
        width: 760px;
    }

    .mp__s1{
        height: 600px;

    }

    .mp__s2{
        height: 1250px;
    }

    .mp__s2 .slider{
        justify-content: space-evenly;
    }

    .mp__s2 .slider .joint__wrapp{
        width: 35%;
        margin-bottom: 30px;
    }

    .mp__s3{
        height: 2050px;
    }

    .mp__s3 .galery .img__wrapp__galery {
        width: 45%;
        margin-bottom: 30px;
    }
    .mp__s3 .galery .img__wrapp__galery:last-child{
        margin-left: 27.5%;
    }
    
}

@media screen and (max-width: 767px){
    .container{
        width: 540px;
    }

    .menu{
        display: none;
    }

    .burger__menu{
        display: flex;
        flex-direction: column;
        position: relative;
        height: 15px;
        width: 30px;

        span{
            position: absolute;
            background: #fff;
            left: 0;
            top: 7px;
            width: 100%;
            height: 2px;

        }
    }
    .burger__menu:before{
        content: '';
        background: #fff;
        height: 2px;
        width: 100%;
        left: 0;
        top: 0;
    }
    .burger__menu:after{
        content: '';
        position: absolute;
        background: #fff;
        height: 2px;
        width: 100%;
        left: 0;
        bottom: 0;
    }

    .mp__s1{
        height: 1200px;
        
    }

    .mp__s1 .advantage__block{
        justify-content: flex-start;
        flex-direction: column;
        align-items: center;
        height: 100%;
    }

    .mp__s1 .advantage__block .block__conent{
        width: 100%;
    }

    .mp__s2{
        height: 2050px;
    }
    
    .mp__s2 .slider{
        justify-content: center;
        align-items: center;
        flex-direction: column;
        flex-wrap: nowrap;
        height: 1500px;
    }

    .mp__s2 .slider .joint__wrapp{
        width: 50%;
    }

    .mp__s3{
        height: 3500px;
    }

    .mp__s3 .galery{
        justify-content: flex-start;
        flex-direction: column;
        flex-wrap: nowrap;
        height: 100%;
    }

    .mp__s3 .galery .img__wrapp__galery{
        width: 65%;
    }
    

    .mp__s3 .galery .img__wrapp__galery:last-child{
        margin-left: 0px;
    }
    
    .mp__s5{
        height: 1500px;
    }

    .mp__s5 .body__wrapp .sturtup{
        justify-content: flex-start;
        align-items: center;
        text-align: center;
        flex-direction: column;
        height: 600px;
    }

    .mp__s5 .body__wrapp .sturtup img{
        width: 75%;
        margin-bottom: 20px;
    }
    .mp__s5 .body__wrapp .sturtup .content{
        width: 75%;
    }

    .mp__s6 .wrapp__section .sponsor__list{
        width: 75%;
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 575px){
    .container{
        width: 100%;
        margin: 0 15px;
    }

    .mp__s2{
        height: 2100px;
    }

}

@media screen and (max-width: 490px){
    
    .container{
        width: 375px;
    }
    
    .mp__s2{
        height: 2270px;
    }

    .mp__s2 .about__body{
        flex-direction: column;
        align-items: center;
    }

    .mp__s2 .about__body .about__content{
        text-align: center;
        width: 85%;
        margin-bottom: 25px;
    }

    .mp__s2 .slider .joint__wrapp{
        width: 65%;
    }

    .mp__s3 .latest__header ul{
        width: 100%;
    }

    .mp__s3 .latest__header ul li{
        margin-right: 10px;
    }

    .mp__s3 .galery .img__wrapp__galery{
        width: 75%;

    }

    .mp__s5 .body__wrapp .sturtup{
        width: 100%;
    }

    .mp__s5 .body__wrapp .sturtup .content .date{
        margin-bottom: 0;
    }

    .mp__s5 .body__wrapp .sturtup .content .date .block {
        width: 35%;
    }

    .mp__s6 .wrapp__section .sponsor__list{
        flex-direction: column;
        flex-wrap: nowrap;
        height: 275px;
        margin-top: 25px;
        margin-bottom: 25px;
    }

    .mp__s6 .wrapp__section .sponsor__list a{
        margin-bottom: 15px;
    }

    .mp__s7 .header__section{
        margin-top: 20px;
    }

    .mp__s7 .body__section{
        flex-direction: column;
    }

    .mp__s7 .body__section nav{
        width: 100%;
    }

    .mp__s7 .body__section nav .nav{
        justify-content: flex-start;
    }

    .mp__s7 .input__wrapp{
        height: 475px;
        width: 100%;
    }

    .mp__s7 .input{
        flex-direction: column;
        
    }

    .mp__s7 .input input{
        width: 100%;

    }

    .mp__s7 .input__footer{
        margin-top: 0;
    }
}

@media screen and (max-width: 375px){
    .container{
        width: 315px;
    }

    .mp__s2 .about__header{
        margin-top: 25px;
    }

    .mp__s2 .slider .joint__wrapp{
        width: 100%;
    }

    .mp__s3 .latest__header ul li{
        margin-right: 7px;
    }

    .mp__s3 .galery .img__wrapp__galery{
        width: 100%;
    }

    .mp__s5{
        height: 1550px;
    }

    .mp__s5 .body__wrapp .sturtup{
        height: 630px;
    }

    .mp__s5 .body__wrapp .sturtup img{
        width: 100%;
    }

    .mp__s5 .body__wrapp .sturtup .content{
        width: 100%;
    }
    
}